html,
body {
  width: 100%;
  height: 100%;
}

body {
  background: white;
  font-family: monospace;
  font-size: 18px;
  text-align: center;
}


.main {
  max-width: 320px;
  margin: 0 auto;
}

#place {
  height: 320px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

@import url('https://fonts.googleapis.com/css?family=Pacifico');

#logo {
  margin-top: 0px;
  margin-bottom: 10px;
  font-family: 'Pacifico', cursive;
  font-size: 5rem;
  background: -webkit-gradient(linear, left top, right top, from(#ef5350), color-stop(#f48fb1), color-stop(#7e57c2), color-stop(#2196f3), color-stop(#26c6da), color-stop(#43a047), color-stop(#eeff41), color-stop(#f9a825), to(#ff5722));
  background: -webkit-linear-gradient(left, #ef5350, #f48fb1, #7e57c2, #2196f3, #26c6da, #43a047, #eeff41, #f9a825, #ff5722);
  background: -o-linear-gradient(left, #ef5350, #f48fb1, #7e57c2, #2196f3, #26c6da, #43a047, #eeff41, #f9a825, #ff5722);
  background: linear-gradient(to right, #ef5350, #f48fb1, #7e57c2, #2196f3, #26c6da, #43a047, #eeff41, #f9a825, #ff5722);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



